.page {
  font-family: "Manrope", sans-serif;
  color: $main-color;

  scroll-behavior: smooth;

  &__body {
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
  
    transition: all 0.3s;
    opacity: 0;
    transform: translateX(100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}
